<template>
    <div class="sidebar" ref="sidebar">
        <el-menu default-active="/data-collection" :router="true" theme="light">
            <el-menu-item index="/data-collection"><i class="el-icon-document"></i></el-menu-item>
            <el-submenu index="1">
                <template slot="title"><i class="el-icon-star-on"></i></template>
                <el-menu-item index="/building-info/list"></el-menu-item>
                <el-menu-item index="/house"></el-menu-item>
            </el-submenu>
            <el-submenu index="2">
                <template slot="title"><i class="el-icon-star-off"></i></template>
                <el-menu-item index="/user-list"></el-menu-item>
                <!-- <el-menu-item index="/unrenter-manager"></el-menu-item> -->
            </el-submenu>
            <el-submenu index="3">
                <template slot="title"><i class="el-icon-message"></i></template>
                <el-menu-item index="/notice"></el-menu-item>
            </el-submenu>
            <!-- <el-menu-item index="4"><i class="el-icon-setting"></i></el-menu-item> -->
        </el-menu>
    </div>
</template>
<script>
    export default {
        mounted() {
            let $sidebar = this.$refs.sidebar;
            $sidebar.querySelector('.el-menu').style.height = document.documentElement.clientHeight - 50 + 'px';
        }
    }
</script>


